import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Welcome from './views/nav1/Welcome.vue'
import AdministratorList from './views/nav1/AdministratorList.vue'
import MenuList from './views/nav2/MenuList.vue'
import WebPermission from './views/nav3/WebPermission.vue'
import AdminPermission from './views/nav3/AdminPermission.vue'
import OrganizationalList from './views/nav3/OrganizationalList'

var routes = [
    {
        path: '/',
        component: Home,
        name:'',
        iconCls: 'fa fa-home',//class
        hidden: true,
        children: [
            { path: '/Welcome', component: Welcome, name: '' }
        ]
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-user-o',//class
        children: [
//          { path: '/main', component: Main, name: '', hidden: true },
            { path: '/AdministratorList', component: AdministratorList, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa el-icon-menu',
        children: [
            { path: '/MenuList', component: MenuList, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa el-icon-setting',
//      leaf: true,//
        children: [
            { path: '/WebPermission', component: WebPermission, name: '' },
            { path: '/AdminPermission', component: AdminPermission, name: '' },
            { path: '/OrganizationalList', component: OrganizationalList, name: '' }
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


